	module.exports = {"data":{"markdownRemark":{"html":"<p>Etter et år i Bergen bosatte den 20 år gamle Grieg seg i København. Han fikk ikke stipendet han hadde søkt på fra departementet, men levde på lånte penger fra faren. Dette var hans første opphold i byen, han hadde bare vært der på gjennomreise et par år tidligere.</p>\n<p>I stipendsøknaden fra 12. juli 1862 er det en selvbevisst ung mann som søker om å få mulighet til å gå videre med studiene, og at han etter tiden i Leipzig «……måskje tør hengi meg til håpet om, ved et fortsatt studium å vinne resultater, som kunne bli av betydning, ikke alene for meg, men også for musikken i mitt fedreland, hvor det er min akt å forbli.»</p>\n<p>I København pleiet han kontakt med danske kunstnervenner, blant andre forfatteren og musikkpedagogen Benjamin Feddersen (1823-1902), en viktig mentor for Grieg, Emil Hornemann (kjent fra studietiden i Leipzig), komponistene Louis Hornbeck og Gottfred Matthison-Hansen, sangeren Julius Steenberg, pianisten August Winding og den unge norske komponisten Rikard Nordraak.</p>\n<p>Poetiske tonebilder er det første verket han skriver i Københavntiden. Samlingen består av seks korte stykker og vi hører her anstrøk av folkemusikk. Noen år senere karakteriserte Grieg selv samlingen som «..noe umodent tøveri..» (brev til Niels Ravnkilde 2. november 1868). Senere i livet skulle Grieg se tilbake på dette verket med langt blidere øyne.  </p>\n<p>Verket er tilegnet Griegs ungdomsvenn, forfatteren og musikklæreren Benjamin Feddersen (1823-1902), som også står bak tekstene til to av Griegs sanger uten opusnummer.</p>","timeToRead":1,"excerpt":"Etter et år i Bergen bosatte den 20 år gamle Grieg seg i København. Han fikk ikke stipendet han hadde søkt på fra departementet, men levde…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus3.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Poetiske tonebilder","composedin":"1864","startDate":"15.6","startTime":null,"location":"Troldhaugen","artists":["Torleif Torgersen"],"medialink":null,"title":"3","dedicatedTo":"Benjamin Feddersen","author":"Monica Jangaard","date":"2017-11-25T20:04:44.484Z","category":"opus","tags":["klaver"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5379310344827586,"src":"/static/opus3-057f01ccb6c009f92f8e1fa66c8a1dbd-1b919.jpg","srcSet":"/static/opus3-057f01ccb6c009f92f8e1fa66c8a1dbd-8172c.jpg 480w,\n/static/opus3-057f01ccb6c009f92f8e1fa66c8a1dbd-1b919.jpg 669w","sizes":"(max-width: 669px) 100vw, 669px"}}}},"fields":{"slug":"/opus3/","artist":[{"frontmatter":{"title":"Torleif Torgersen","category":"artister","tags":["pianist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-466fa.jpg","srcSet":"/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-27b23.jpg 90w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-0afbd.jpg 180w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-466fa.jpg 360w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-43088.jpg 540w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-545bd.jpg 720w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-8c1c4.jpg 1080w,\n/static/TorleifTorgersen-c83beeea8bcd2d888fe5a4bcb1b38b5f-1a344.jpg 1920w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/torleif-torgersen/","opuses":[{"frontmatter":{"title":"10"}},{"frontmatter":{"title":"3"}},{"frontmatter":{"title":"44"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus3/"}}

